    z-index: 20;
  }
  
  .try_it_now-y span{
  position: relative;
  pointer-events: none;
  }
  
  .try_it_now-y::before{
    --size:0;
    content:'';
    position: absolute;
    left: var(--x);
    top: var(--y);
    width: var(--size);
    height: var(--size);
    background: radial-gradient(circle closest-side, #4405f7, transparent);
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
